<template>
  <div class="chat-container">
    <!--  -->
    <van-nav-bar left-arrow fixed class="page-navbar" title="" @click-left="$router.back()"></van-nav-bar>
    <!-- / -->

    <!--  -->
    <div class="message-list" ref="message-list">
      <div
        class="message-item"
        v-for="(item, index) in messages"
        :key="index"
        :class="{reverse: item.userId === user.user_id}"
      >
        <van-image round class="avatar" :src="item.photo"></van-image>
        <div class="msg-wrap" style="flex: 1;">
          <div class="title">{{ item.msg }}</div>
        </div>
      </div>
    </div>
    <!-- / -->

    <!--  -->
    <van-cell-group class="send-message">
      <van-field v-model="message" center clearable>
        <van-button slot="button" type="primary" size="small" @click="onSend"></van-button>
      </van-field>
    </van-cell-group>
    <!-- / -->
  </div>
</template>
<script>
import io from 'socket.io-client'
import { mapState } from 'vuex'
import { setItem, getItem } from '@/utils/storage'

export default {
  name: 'UserChat',
  props: {},
  components: {},
  data() {
    return {
      message: '',
      messages: getItem('chat-messages') || [], // 
      socket: null // WebSocket 
    }
  },
  computed: {
    ...mapState(['user'])
  },
  watch: {
    messages (value) {
      /*
        
          1
          2
       */
      setItem('chat-messages', value)
      // 
      this.$nextTick(() => {
        this.scrollToBottom()
      })
    }
  },
  created() {
    //  WebSocket 
    //  WebSocket   axios 
    const socket = io('http://ttapi.research.itcast.cn')
    this.socket = socket

    socket.on('connect', function() {
      console.log('')
    })

    //  
    // socket.emit('message', this.message)
    // 
    // window.socket = socket

    // 
    socket.on('message', data => {
      data.photo = 'http://toutiao.meiduo.site/FqHn7ps9v5I8esWXJNKH0asrSwcB'
      this.messages.push(data)
    })
  },
  mounted() {
    this.scrollToBottom()
  },
  methods: {
    onSend() {
      const message = this.message.trim()

      // 
      if (!message.length) {
        return
      }

      //  
      const data = {
        msg: message,
        timestamp: Date.now(),
        photo: 'http://toutiao.meiduo.site/FlOAkWHoU8lnYwU6eCEPN-dHINHl',
        userId: this.user.user_id
      }
      this.socket.emit('message', data)

      // 
      this.messages.push(data)

      // 
      this.message = ''
    },
    scrollToBottom () {
      const messageList = this.$refs['message-list']
      // 
      // 
      // API, DOM.
      // 
      this.$nextTick(() => {
        // messageList.scrollTop 
        // messageList.scrollTop = xxx 
        messageList.scrollTop = messageList.scrollHeight
      })
    }
  }
}
</script>
<style scoped lang="less">
.chat-container {
  position: absolute;
  top: 0;
  left: 0;
  padding: 46px 0 50px;
  box-sizing: border-box;
  background-color: #f5f5f6;
  width: 100%;
  height: 100%;
  .message-list {
    overflow-y: scroll;
    height: 100%;
    .message-item {
      display: flex;
      flex-flow: row nowrap;
      align-items: flex-start;
      padding: 10px;
      .avatar {
        width: 40px;
        height: 40px;
        margin-right: 5px;
      }
      .msg-wrap {
        word-break: break-all;
        word-wrap: break-word;
        .title {
          display: table;
          border-radius: 6px;
          font-size: 14px;
          padding: 10px;
          margin-right: 45px;
          background-color: #e0effb;
        }
      }
    }
    .reverse {
      flex-direction: row-reverse;
      .msg-wrap {
        display: flex;
        flex-direction: row-reverse;
        .title {
          margin-right: 5px;
          margin-left: 45px;
        }
      }
    }
  }
  .send-message {
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background-color: #f5f5f6 !important;
  }
}
</style>
